    "abstract": [{
      "lang": "DE",
      "text": "Die vorliegende Erfindung betrifft ein Verfahren zum Erzeugen einer Statistik von Phondauern und ein Verfahren zum Ermitteln der Dauer einzelner Phone fr die Sprachsynthese. Erfindungsgem wird eine Primrstatistik vorgesehen, die beispielsweise auf Primrklustern (z.B. Triphonen) beruht und eine Sekundrstatistik, die auf Sekundrklustern (z.B. Phoneme von ganzen Wrtern) beruht. Beide Statistiken beinhalten mittlere Phondauern und beispielsweise die Standardabweichung der mittleren Phondauern. Bei der Ermittlung der Phondauern wird zunchst versucht, diese anhand der Sekundrstatistik, die sprachspezifischer ist, zu ermitteln. Falls dies nicht der Fall ist, wird auf die Primrstatistik zurckgegriffen, die immer anwendbar ist. Durch dieses zweistufige Verfahren wird eine Phondauer ermittelt, die einer natrlichen Sprache wesentlich besser entspricht, als dies mit dem bekannten einstufigen Verfahren mglich war."
    }],
