    "abstract": [{
      "lang": "EN",
      "text": "The invention provides a ultraviolet laser device for cutting a large-width Micro Phone chip, comprising an ultraviolet laser and a processing platform, wherein, an output end of the ultraviolet laser is provided with an optical shutter, an output end of the optical shutter is connected with a beam-expanding lens, an output end of the beam-expanding lens is provided with a first reflection mirror, the first reflection mirror is connected with a second reflection mirror which is connected with a third reflection mirror, an output end of the third reflection mirror is connected with a focusing mirror, and the focusing mirror faces directly towards the processing platform; the laser emitted by the ultraviolet laser enters the optical shutter and then perpendicularly enters the beam-expanding lens through the optical shutter, the laser passing through the beam-expanding lens enters the first reflection mirror, the second reflection mirror and the third reflection mirror in sequence, and the reflected laser perpendicularly enters the focusing mirror so as to be focused on the processing platform through the laser of the focusing mirror. The invention realizes the cutting of the large-width Micro Phone silicon chip by ultraviolet laser with the cutting width reaching 8 inches at most, fast cutting speed, high processing efficiency and quite excellent practicability."
    }, {
      "lang": "CN",
      "text": "Micro PhoneMicro Phone8"
    }],
